
@keyframes stretch {
    0% {
      transform: scale(.6);
    }
    50% {
        transform: scale(1.2);
    }
    100% {
        transform: scale(1.7);
    }
  }

  @media (max-width: 415px) { 
    .preLogo {
      height: 250px;
      margin-top: 20%;
      animation-name: stretch;
      animation-duration: 1.5s; 
       animation-timing-function: ease-out; 
       animation-delay: 0;
      animation-direction: alternate;
      animation-iteration-count: infinite;
      animation-fill-mode: none;
       animation-play-state: runnin
  }
  .preLoader{
    font-size: 30px;
    font-family: "Caveat", cursive;
    opacity: .5;
  }
  }

  @media (min-width: 420px){
    .preLogo {
      opacity: .3;
      height: 350px;
      animation-name: stretch;
      animation-duration: 1.5s; 
       animation-timing-function: ease-out; 
       animation-delay: 0;
      animation-direction: alternate;
      animation-iteration-count: infinite;
      animation-fill-mode: none;
       animation-play-state: runnin
  }
  .preLoader{
    font-size: 90px;
    letter-spacing: 10px;
    padding-top: 5%;
    font-family: "Caveat", cursive;
    opacity: .5;
  }


  }